import React from "react";
import "./App.css";

import moment from "moment-timezone/moment-timezone";
import timezones from "./timezones.json";

moment.tz.load(timezones);

function App() {
  return (
    <div className="App">
      <div>Moment timezone: {moment.tz.guess()}</div>
      <div>Browser timezone offset: GMT{String(new Date().getTimezoneOffset() / -60).replace(/^(\d)/, "+$1")}</div>
    </div>
  );
}

export default App;
